import React from 'react'
import { useSelector } from 'react-redux'
import Dropdown from 'Component/Dropdown'
import Decimal from 'Component/Input/decimal'
import Input from 'Component/Input'
import InputText from 'Component/Input/text'


const WeightEntry = ({
    entryListIdx,
    wmsPoSkuInfoId,
    outboundTypeList,
    reason,
    inputWidth,
    showTitle,
    item,
    onChange,
    onChangeDropdown,
    uom,
    totalPack,
    poListIdx,
    category
}) => {
    const ripenessLevel = useSelector(state => state.ripenessLevel)
    return (
        <div className='d-flex w-100 we-m'>
            <Input
                isEmpty={item.qtyIsEmpty}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                inputWidth={inputWidth}
                values={item?.qty}
                showTitle={showTitle}
                title={'Input Quantity'}
                onChange={(e) => onChange(e)} />

            <Decimal
                isEmpty={item.qtyIsEmpty}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                inputWidth={inputWidth}
                values={item?.qty}
                title={'Input Weight'}
                showTitle={showTitle}
                onChange={(e) => onChange(e)} />
            <Input
                isEmpty={item.packIsEmpty}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={'PACK'}
                inputWidth={`${inputWidth} ${totalPack ? null : 'd-none'}`}
                values={item?.pack}
                showTitle={showTitle}
                title={'Pack'}
                name='pack'
                onChange={(e) => onChange(e)} />
            <Dropdown
                isEmpty={item.outboundTypeIsEmpty}
                ddType={'codeOutbound'}
                selectedValue={item?.codeOutbound}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={'kg'}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'codeOutbound' })}
                title='Select Outbound Type'
                placeholder='Select Outbound Type'
                options={outboundTypeList}
                width='w-30 text-nowrap'
                showTitle={showTitle}
                show={true}
            />
            <Dropdown
                // isDisabled={entryListIdx === 0 ? true : false}
                isEmpty={item.ripenessIsEmpty}
                selectedValue={item?.ripeness}
                grade={item.grade?.label}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                show={true}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'ripeness' })}
                title='Select Ripeness'
                placeholder='Select Ripeness'
                options={ripenessLevel}
                width='w-30'
                showTitle={showTitle}
                className={item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste' ? 'd-none' : (category === 'Fruits' ? null : 'd-none')} />
            <InputText
                isEmpty={item.noteIsEmpty}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                inputWidth={'w-30'}
                values={item?.note}
                showTitle={showTitle}
                title={'Note'}
                onChange={(e) => onChange(e)} />
        </div>
    )
}
export default WeightEntry