import React, { useState } from 'react'
import { Redirect } from 'react-router-dom'
import { useSelector } from 'react-redux'
import SelectedSKUList from 'Page/StockEntry/AdditionalOutbound/WeightEntry/SelectedSKUList'
import WeightEntryList from 'Page/StockEntry/AdditionalOutbound/WeightEntry/WeightEntryList'


const WeightEntry = (props) => {
    const outboundEntryData = useSelector(state => state.outboundEntryData)
    const [hideSKUList, setHideSKUList] = useState(false)
    const [activeSku, setActiveSKU] = useState(0)
    const [allowPage, setAllowPage] = useState([
        '/inventory-entry/additional-outbound/weight-entry',
        '/inventory-entry/additional-outbound/remove-sku',
        '/inventory-entry/additional-outbound/add-sku',
    ])
    let height = document.documentElement.clientHeight
    let clientWidth = document.documentElement.clientWidth
    let heightAdj = height - 150
    const moduleName = 'outbound'
    if (!outboundEntryData || outboundEntryData?.jobPost.list.length === 0) {
        return (
            <div className='d-flex align-items-center justify-content-center text-center w-100 h-screen'><Redirect to='/inventory-entry/additional-outbound'>Please Select At Least 1 SKU</Redirect></div >)
    }
    return (
        <div style={{height: clientWidth <= 500 ? height : heightAdj }} className='w-100 d-md-flex justify-content-between scroll-y z-0'>
            <SelectedSKUList
                hideSKUList={hideSKUList}
                activeSku={activeSku}
                setActiveSKU={setActiveSKU}
                moduleName={moduleName}
                moduleEntry={outboundEntryData}
            />
            <WeightEntryList
                setHideSKUList={setHideSKUList}
                hideSKUList={hideSKUList}
                moduleName={moduleName}
                moduleEntry={outboundEntryData}
                activeSku={activeSku}
            />
        </div>
    )
}

export default WeightEntry