import { CREATE_JOB_SHEET } from 'apiServices/action'
import {totalListItemWeightCount} from 'Page/StockEntry/GeneralServices/moduleServices'

export const updateJSWeightLine = ({
    dispatch,
    moduleEntry,
    moduleName,
    activeSku,
    poListIdx,
    entryListIdx,
    e
}) => {
    const name = e?.target?.name
    let value = e?.target?.value
    let js = { ...moduleEntry }

    console.clear()

    if (name === 'weight' || name === 'weightToCompare') {
        // js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qty = parseInt(value)
        let regex = /^[0-9\.\x0]+$/
        console.clear()
        if ((regex.test(value) === true && value !== "")) {
            let newValue = 0;
        let arr = value?.split('.')
        let number = arr[0];
        if(number >= 0) number = parseInt(number)
        let commaNumber = arr[1];
        
        if ((commaNumber !== null && commaNumber !== undefined)) {
            if (commaNumber.length > 2) {
                //jika dibelakang koma ada lebih dari 2 angka
                var sliceNumber = commaNumber.slice(0, 2);
                newValue = `${number}.${sliceNumber}`
            } else {
                newValue = `${number}.${commaNumber}`
            }
        } else {
            newValue = parseInt(number)
        }
        
            //fill weight qty
            if(name === 'weight'){
                js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qty = newValue
                js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qtyIsEmpty = false
            }
            if(name === 'weightToCompare'){
                js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qtyToCompare = newValue
                js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qtyToCompareIsEmpty = false
            }

        }
        else{
            //fill weight qty
            if(name === 'weight'){
                js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qty = ''
            }
            if(name === 'weightToCompare'){
                js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qtyToCompare = ''
            }   
        }
    }

    else if (name === 'qty'){
        let regex = /^[0-9.]+$/
        if ((regex.test(value) === false && value !== "")) {
            return;
        }
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qty = value ? parseInt(value) : value
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qtyIsEmpty = false
    }

    else if (name === 'pack'){
        let regex = /^[0-9.]+$/
        if ((regex.test(value) === false && value !== "")) {
            return;
        }
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].pack = value > 0 ? parseInt(value) : value
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].packIsEmpty = false
    }
    if (name === 'reason') { 
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].note = value 
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].noteIsEmpty = false
    }
    
    const initialWeight = js.jobPost.list[activeSku].poList[poListIdx].inboundQty
    let initialWeightUpdate = initialWeight
    js.jobPost.list[activeSku].poList[poListIdx].edited = true
    js.jobPost.list[activeSku].poList[poListIdx].minus = false
    js.jobPost.list[activeSku].poList[poListIdx].itemList.map(data => {
        initialWeightUpdate = initialWeightUpdate - (!isNaN(data.qty) ? data.qty : 0)
        js.jobPost.list[activeSku].poList[poListIdx].initialWeightUpdate = initialWeightUpdate
        if (initialWeightUpdate < 0) {
            js.jobPost.list[activeSku].poList[poListIdx].minus = false
            js.jobPost.list[activeSku].poList[poListIdx].initialWeightUpdate = 0
        }
        else{
            js.jobPost.list[activeSku].poList[poListIdx].itemList.filter(item => {return item.qtyIsEmpty=false})
        }
    })

    //initial weight from it self
    const initialWeightScreen = js.jobPost.list[activeSku].poList[poListIdx].initialWeight
    let initialWeightScreenUpdate = initialWeightScreen
    js.jobPost.list[activeSku].poList[poListIdx].itemList.map(data => {
        initialWeightScreenUpdate = initialWeightScreenUpdate - (!isNaN(data.qty) ? data.qty : 0)
        js.jobPost.list[activeSku].poList[poListIdx].initialWeightScreenUpdate = initialWeightScreenUpdate
    })
    //end of initial weight from it self

    const over = totalListItemWeightCount({moduleList:js.jobPost.list[activeSku].poList[poListIdx].itemList})
    js.jobPost.list[activeSku].poList[poListIdx].initialWeightOver = over > initialWeight ? over : 0

    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
}

export const updateJSWeightLineDropdown = ({
    dispatch,
    moduleEntry,
    moduleName,
    activeSku,
    poListIdx,
    entryListIdx,
    selected,
    name
}) => {
    let js = { ...moduleEntry }
    if (name === 'codeOutbound') {
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].codeOutbound = selected
        js.jobPost.list[activeSku].poList[poListIdx].edited = true
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].outboundTypeIsEmpty = false
    }
    if(name === 'ripeness'){
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].ripeness = selected
        js.jobPost.list[activeSku].poList[poListIdx].edited = true
    }
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
}

export const addRow = ({
    dispatch,
    moduleEntry,
    moduleName,
    activeSku,
    poListIdx,
}) => {
    let js = { ...moduleEntry }
    js.jobPost.list[activeSku].poList[poListIdx].itemList.push({
        qty: '',
        codeOutbound: { label: '', value: '' },
        note: ''
    })
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
}


